import Vue from 'vue'
import Router from 'vue-router'
//
import AdminLogin from '@/components/admin/login'
import AdminHome from '@/components/admin/home'
import AdminIndex from '@/components/admin/index'
import AdminError from '@/components/admin/error'
import AdminGoods from '@/components/admin/goodslist'
import AdminAddGoods from '@/components/admin/addgoods'
import AdminType from '@/components/admin/typelist'
import AdminAddKm from '@/components/admin/addkm'
import AdminKm from '@/components/admin/kmlist'
import AdminSwiper from '@/components/admin/swiper'
import AdminSet from '@/components/admin/common'
import AdminPromotion from '@/components/admin/promotion'
import AdminPay from '@/components/admin/pay'
import AdminEmail from '@/components/admin/email'
import AdminOrder from '@/components/admin/order'
import AdminTemplate from '@/components/admin/template'
import AdminLogo from '@/components/admin/logo'
//
import Index from '@/components/index'
import Goods from '@/components/goods'
import Query from '@/components/query'

import Install from '@/components/install/index'
Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/admin',
      component: AdminHome,
      beforeEnter:(to,from,next)=>{
        if (sessionStorage.getItem('token')) {
          next()
        } else {
          next('/bf-login')
        }
      },
      children: [
        {
          path: 'index',
          component: AdminIndex,
          meta: { title: '' }
        },
        {
          path: 'order',
          component: AdminOrder,
          meta: { title: '' }
        },
        {
          path: 'goods',
          component: AdminGoods,
          meta: { title: '' }
        },
        {
          path: 'addgoods',
          component: AdminAddGoods,
          meta: { title: '' }
        },
        {
          path: 'type',
          component: AdminType,
          meta: { title: '' }
        },
        {
          path: 'promotion',
          component: AdminPromotion,
          meta: { title: '' }
        },
        {
          path: 'addkm',
          component: AdminAddKm,
          meta: { title: '' }
        },
        {
          path: 'kmlist',
          component: AdminKm,
          meta: { title: '' }
        },
        {
          path: 'common',
          component: AdminSet,
          meta: { title: '' }
        },
        {
          path: 'swiper',
          component: AdminSwiper,
          meta: { title: '' }
        },
        {
          path: 'pay',
          component: AdminPay,
          meta: { title: '' }
        },
        {
          path: 'email',
          component: AdminEmail,
          meta: { title: '' }
        },
        {
          path: 'template',
          component: AdminTemplate,
          meta: { title: '' }
        },
        {
          path: 'logo',
          component: AdminLogo,
          meta: { title: 'LOGO' }
        },
       
        {
          path: '*',
          redirect: '/error'
        },
        {
          path: '/',
          redirect: 'error'
        }
      ]
    },
    {
      path: '/error',
      component: AdminError,
      meta: { title: '' }
    },
    {
      path: '/',
      component: Index,
      meta: { title: '' }
    },
    {
      path: '/install',
      component: Install,
      meta: { title: '' }
    },
    {
      path: '/goods/:id.html',
      component: Goods,
      meta: { title: '' }
    },
    {
      path: '/query',
      component: Query,
      meta: { title: '' }
    },
   
    {
      path: '/bf-login',
      component: AdminLogin,
      meta: { title: '' }
    },
    {
      path: '*',
      redirect: '/'
    },
  ]
})



